"use client";

import { useState, useEffect, useCallback } from "react";
import { motion, AnimatePresence } from "motion/react";

export default function Header() {
  const [isScrolled, setIsScrolled] = useState(false);
  const [isFormOpen, setIsFormOpen] = useState(false);
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [isHeaderVisible, setIsHeaderVisible] = useState(true);

  // Throttled scroll handler
  const handleScroll = useCallback(() => {
    const scrollPosition = window.scrollY;
    setIsScrolled(scrollPosition > 200);  // Show sticky header after 100px
    setIsHeaderVisible(scrollPosition <= 200);  // Hide initial header after 100px
  }, []);

  useEffect(() => {
    const throttledScroll = () => {
      let ticking = false;
      return () => {
        if (!ticking) {
          window.requestAnimationFrame(() => {
            handleScroll();
            ticking = false;
          });
          ticking = true;
        }
      };
    };

    window.addEventListener("scroll", throttledScroll());
    return () => window.removeEventListener("scroll", throttledScroll());
  }, [handleScroll]);

  const toggleForm = () => setIsFormOpen(!isFormOpen);
  const toggleMenu = () => setIsMenuOpen(!isMenuOpen);

  return (
    <>
      {/* Initial Header - visible until 100px scroll */}
      <AnimatePresence>
        {isHeaderVisible && (
          <motion.header
            initial={{ y: 0 }}
            exit={{ y: -100 }}
            transition={{ duration: 0.5, ease: "easeOut" }}
            className="fixed top-0 left-0 right-0 z-50"
          >
            <div className="px-4 lg:px-8 py-4 lg:py-6 flex justify-between items-center">
              <motion.div
                initial={{ y: "100%" }}
                animate={{ y: "0%" }}
                transition={{ duration: 1, delay: 0.3, ease: [0.16, 1, 0.3, 1] }}
                className="font-bold text-2xl md:text-6xl text-white"
              >
                OH Architecture
              </motion.div>
              
              {/* Desktop Navigation */}
              <div className="hidden lg:flex items-center gap-8">
                <div className="flex gap-4 font-bold text-lg text-white uppercase mr-96">
                  {['Works,', 'Studio,', 'Process,', 'Gallery'].map((item) => (
                    <HoverAnimationLink key={item} text={item} />
                  ))}
                </div>
                <GetInTouchButton onClick={toggleForm} />
              </div>
              
              <div className="lg:hidden">
                <MobileMenuButton onClick={toggleMenu} />
              </div>
            </div>
          </motion.header>
        )}
      </AnimatePresence>

      {/* Sticky Header (appears after 100px scroll) */}
      <AnimatePresence>
        {isScrolled && (
          <motion.header 
            initial={{ y: 100 }}
            animate={{ y: 0 }}
            exit={{ y: 100 }}
            transition={{ duration: 0.5, ease: "easeOut" }}
            className="fixed top-0 left-0 right-0 z-50"
          >
            <div className="px-4 lg:px-8 py-4 flex justify-end items-center">
              <div className="flex gap-4">
                <GetInTouchButton onClick={toggleForm} />
                <MenuButton onClick={toggleMenu} />
              </div>
            </div>
          </motion.header>
        )}
      </AnimatePresence>

      {/* Slide-in Contact Form */}
      <AnimatePresence>
        {isFormOpen && (
          <motion.div
            initial={{ x: "100%" }}
            animate={{ x: 0 }}
            exit={{ x: "100%" }}
            transition={{ duration: 0.7, ease: [0.16, 1, 0.3, 1] }}
            className="fixed inset-y-0 right-0 w-full lg:w-[40%] bg-black z-[100] p-8"
          >
            <div className="flex flex-col h-full">
              <div className="flex justify-between items-start">
                <div className="text-white">
                  <h2 className="text-xl mb-6">PROJECT ENQUIRY</h2>
                  <p className="text-gray-300 max-w-md">
                    To get started, we ask that you provide some<br />
                    initial information about your project to help<br />
                    us determine whether our studio is the<br />
                    right fit. Our team will review the details and<br />
                    be in touch to discuss next steps within 5<br />
                    business days.
                  </p>
                </div>
                
                <div className="flex flex-col items-end">
                  <button 
                    onClick={toggleForm}
                    className="text-white text-base uppercase tracking-wider mb-4"
                  >
                    Close
                  </button>
                  <div className="w-32 h-32 bg-gray-700 rounded-lg overflow-hidden">
                    {/* Placeholder for image */}
                    <div className="w-full h-full bg-gray-600 flex items-center justify-center">
                      <span className="text-gray-400">Image</span>
                    </div>
                  </div>
                </div>
              </div>
              
              <div className="mt-12 flex items-start justify-between">
                <div className="text-white text-lg">Your personal details</div>
                <div className="text-gray-400 text-lg">(01/08)</div>
              </div>
              
              <div className="border-b border-gray-700 my-4"></div>
              
              <div className="flex-1 mt-8">
                <div className="space-y-6">
                  <FormInput label="Name *" placeholder="Your Name" />
                  <FormInput label="Email Address *" placeholder="Your Email Address" />
                  <FormInput label="Phone Number" placeholder="07 1234 5678" />
                </div>
              </div>
              
              <div className="flex justify-end mt-8">
                <button
                  className="px-6 py-3 bg-white text-black font-medium rounded-full flex items-center gap-2"
                >
                  Next
                  <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className="w-5 h-5">
                    <path strokeLinecap="round" strokeLinejoin="round" d="M13.5 4.5 21 12m0 0-7.5 7.5M21 12H3" />
                  </svg>
                </button>
              </div>
            </div>
          </motion.div>
        )}
      </AnimatePresence>

      {/* Mobile Menu */}
      <AnimatePresence>
        {isMenuOpen && (
          <motion.div
            initial={{ y: "-100%" }}
            animate={{ y: 0 }}
            exit={{ y: "-100%" }}
            transition={{ duration: 0.7, ease: [0.16, 1, 0.3, 1] }}
            className="fixed inset-0 bg-black z-[100] p-8"
          >
            <div className="flex flex-col h-full">
              <div className="flex justify-between">
                <div className="font-bold text-2xl text-white">OH Architecture</div>
                <button 
                  onClick={toggleMenu}
                  className="text-white text-2xl"
                >
                  Ã—
                </button>
              </div>
              
              <div className="flex-1 flex flex-col justify-center">
                {['Home', 'Works', 'Studio', 'Process', 'Gallery'].map((item) => (
                  <HoverAnimationLink 
                    key={item} 
                    text={item} 
                    className="text-3xl lg:text-4xl font-light py-3 text-white" 
                  />
                ))}
              </div>
              
              <div className="pb-8">
                <div className="mb-6">
                  <a href="#" className="text-lg text-white">Instagram</a>
                </div>
                <div className="flex gap-6">
                  <a href="#" className="text-gray-400">Privacy Policy</a>
                  <a href="#" className="text-gray-400">Terms of Services</a>
                </div>
              </div>
            </div>
          </motion.div>
        )}
      </AnimatePresence>
    </>
  );
}

interface ButtonProps {
  onClick: () => void;
}

function GetInTouchButton({ onClick }: ButtonProps) {
  return (
    <motion.button
      initial={{ opacity: 0, y: 16 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 1, delay: 0.7, ease: [0.16, 1, 0.3, 1] }}
      onClick={onClick}
      className="group cursor-pointer relative flex items-center uppercase"
      aria-label="Get in touch"
    >
      <div className="flex items-center px-5 h-10 lg:h-12 rounded-full bg-neutral-900 text-neutral-100 font-semibold text-[clamp(14px,1.2vw,18px)] border border-neutral-800">
        <div className="overflow-hidden h-6">
          <div className="flex flex-col transition-transform duration-500 ease-[cubic-bezier(0.25,1,0.5,1)] group-hover:-translate-y-1/2">
            <span className="text-[clamp(14px,1vw,18px)] text-neutral-100 font-semibold mb-1.5 uppercase">
              Get in touch
            </span>
            <span className="text-[clamp(14px,1vw,18px)] text-neutral-100 font-semibold mb-1.5">
              Get in touch
            </span>
          </div>
        </div>
      </div>
    </motion.button>
  );
}

function MenuButton({ onClick }: ButtonProps) {
  return (
    <button 
      onClick={onClick}
      className="group cursor-pointer relative font-bold text-[14px] lg:text-[16px] rounded-3xl px-4 py-2 bg-white uppercase"
      aria-label="Menu"
    >
      <div className="overflow-hidden h-[1.5em]">
        <div className="flex flex-col transition-transform duration-500 ease-[cubic-bezier(0.25,1,0.5,1)] group-hover:-translate-y-1/2">
          <span className="mb-1.5">Menu</span>
          <span className="mb-1.5">Menu</span>
        </div>
      </div>
    </button>
  );
}

function MobileMenuButton({ onClick }: ButtonProps) {
  return (
    <button 
      onClick={onClick}
      className="group cursor-pointer relative font-bold text-[14px] rounded-3xl px-4 py-2 text-white uppercase"
      aria-label="Menu"
    >
      <div className="overflow-hidden h-[1.5em]">
        <div className="flex flex-col transition-transform duration-500 ease-[cubic-bezier(0.25,1,0.5,1)] group-hover:-translate-y-1/2">
          <span className="mb-1.5">Menu</span>
          <span className="mb-1.5">Menu</span>
        </div>
      </div>
    </button>
  );
}

interface LinkProps {
  text: string;
  className?: string;
}

function HoverAnimationLink({ text, className = "" }: LinkProps) {
  return (
    <a 
      href="#" 
      className={`group block overflow-hidden ${className}`}
    >
      <div className="overflow-hidden h-[1.5em]">
        <div className="flex flex-col transition-transform duration-500 ease-[cubic-bezier(0.25,1,0.5,1)] group-hover:-translate-y-1/2">
          <span className="mb-1.5">
            {text}
          </span>
          <span className="mb-1.5">
            {text}
          </span>
        </div>
      </div>
    </a>
  );
}

interface FormInputProps {
  label: string;
  placeholder: string;
}

function FormInput({ label, placeholder }: FormInputProps) {
  return (
    <div>
      <label className="text-gray-400 text-sm">{label}</label>
      <input 
        type="text" 
        placeholder={placeholder}
        className="w-full mt-1 p-3 bg-[#292929] text-white placeholder-gray-500 focus:outline-none"
      />
    </div>
  );
}